.Message {
  display: flex;
  align-items: flex-end;
  margin-bottom: 0.375rem;

  &:last-child {
    margin-bottom: 0;
  }

  &.own {
    flex-direction: row-reverse;

    &:first-child:not(:only-child) .content {
      border-bottom-right-radius: var(--border-radius-messages-small);
    }

    &:not(:first-child):not(:last-child):not(:only-child) .content {
      border-top-right-radius: var(--border-radius-messages-small);
      border-bottom-right-radius: var(--border-radius-messages-small);
    }

    &:last-child:not(:only-child) {
      border-top-right-radius: var(--border-radius-messages-small);
    }

    &:last-child .content {
      &.text {
        border-bottom-right-radius: 0;

        &::before {
          content: '';
          display: block;
          position: absolute;
          right: -.64rem;
          bottom: -.25rem;
          width: .7rem;
          height: 1.2rem;
          // background-image: url('../../../../assets/chat-bubble-green.svg');
          background-image: url(data:image/svg+xml;base64,);
          background-position: bottom right;
        }
      }
    }
  }

  &:not(.own) {
    &:first-child:not(:only-child) .content {
      border-bottom-left-radius: var(--border-radius-messages-small);
    }

    &:not(:first-child):not(:last-child):not(:only-child) .content  {
      border-top-left-radius: var(--border-radius-messages-small);
      border-bottom-left-radius: var(--border-radius-messages-small);
    }

    &:last-child:not(:only-child) {
      border-top-left-radius: var(--border-radius-messages-small);
    }

    &:last-child .content {
      &.text {
        border-bottom-left-radius: 0;

        &::before {
          content: '';
          display: block;
          position: absolute;
          bottom: -.24rem;
          left: -.64rem;
          width: .75rem;
          height: 1.2rem;
          // background-image: url('../../../../assets/chat-bubble-white.svg');
          background-image: url(data:image/svg+xml;base64,);
          background-size: auto;
          background-position: bottom left;
        }
      }
    }
  }

  .Avatar {
    margin-right: 0.3rem;
  }

  .content {
    position: relative;
    max-width: 29rem;
  }

  .text {
    border-radius: var(--border-radius-messages);
    background: white;
    padding: .35rem .5rem;
    // padding-right: 3rem;
    font-size: 0.9375rem;
    box-shadow: 0 1px 2px rgba(114, 114, 114, 0.25);

    .long-word-break-all {
      word-break: break-all;
    }

    p {
      margin: 0;

      &::after {
        content: '';
        display: inline-block;
        width: 4rem;
        height: 1rem;
      }
    }

    a {
      color: var(--color-links);
      text-decoration: none;

      &:hover, &:active, &:visited {
        color: var(--color-links-hover);
        text-decoration: underline;
      }
    }
  }

  .emoji-only-1, .emoji-only-2, .emoji-only-3 {
    line-height: 1;
    margin-top: 0.5rem;
    margin-bottom: -0.5rem;

    &.emoji-only-1 {
      font-size: 5rem;
    }

    &.emoji-only-2 {
      font-size: 4rem;
    }

    &.emoji-only-3 {
      font-size: 3rem;
    }
  }

  &.own {
    .text {
      background-color: #EEFEDF;
    }

    .content {
      max-width: 30rem;
    }
  }
}
